.SearchResults {
  box-sizing: border-box;
  width: 43%;
  max-height: 700px;
  overflow-y: scroll;
  padding: 0.88rem;
  background-color: transparent;
  box-shadow: #8038927c 0px 7px 29px 0px;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

@media only screen and (max-width: 1020px) {
  .SearchResults {
    width: 90%;
    margin-bottom: 2rem;
  }
}
