.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 6.94rem;
  margin-bottom: 6.33rem;
}

.SearchBar input {
  background-color: #ebe4f5;
  width: 250px;
  padding: 13px 0;
  border: 1px solid #ebe4f5;
  margin-bottom: 2.22rem;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
  box-shadow: #2d5b757c 0px 7px 29px 0px;
  border-radius: 0;
}

@media only screen and (min-width: 600px) {
  .SearchBar input {
    width: 450px;
    padding: 0.88rem 0;
  }
}
.SearchBar input:focus {
  outline: none;
}

.SearchButton {
  cursor: pointer;
  width: 8.11rem;
  padding: 13px 0;
  background-color: transparent;
  text-align: center;
  font-size: 0.833rem;
  transition: background-color 0.25s;
  border: 1px solid #ebe4f5;
  color: #ebe4f5;
  font-weight: 500;
  transition: 0.3s ease-in;
}

.SearchButton:hover {
  background-color: #ebe4f5;
  color: rgb(117, 114, 119);
}
