.Playlist {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  width: 43%;
  max-height: 700px;
  padding: 2.27rem 1.16rem;
  background-color: rgba(255, 0, 0, 0);
  box-shadow: #8038927c 0px 7px 29px 0px;
  margin: 0 auto;
}

.Playlist input {
  width: 100%;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid #ebe4f5;
  font-family: "Poppins", sans-serif;
  font-size: 1.55rem;
  color: #ebe4f5;
  text-align: center;
  margin-bottom: 20px;
}

.Playlist-save {
  cursor: pointer;
  width: 10rem;
  padding: 13px 0;
  border-width: 0px;
  margin-top: 20px;
  background-color: rgba(255, 0, 0, 0);
  border: 1px solid #ebe4f5;
  text-align: center;
  font-size: 0.83rem;
  transition: background-color 0.25s;
  color: #ebe4f5;
  font-weight: 500;
  margin: 20px 0;
  min-height: 45px;
}

.Playlist-save:hover,
.Playlist-save:active {
  background-color: #ebe4f5;
  color: rgb(117, 114, 119);
}
input:focus::placeholder {
  color: rgba(255, 0, 0, 0);
}
.Playlist ::placeholder {
  color: #f3f0f0;
  font-size: 0.6em;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

@media only screen and (max-width: 1020px) {
  .Playlist {
    width: 90%;
  }
}
