body,
html,
#root {
  height: auto;
  background-image: radial-gradient(circle, #d464f5, #ca5df1, #c056ed, #b650ea, #ac49e6, #a543e3, #9e3ce1, #9636de, #902ddb, #8a23d9, #8417d6, #7e05d4);
  background-size: cover;
  background-repeat: no-repeat;
}

html {
  font-size: 18px;
}

h1 {
  text-align: center;
  font-family: "Fjalla One", "Poppins", sans-serif;
  font-size: 2rem;
  padding-top: 10rem;
  margin-top: 0;
  margin-bottom: 0;
  text-shadow: 1px 1px rgba(119, 118, 118, 0.39);
}
@media only screen and (min-width: 600px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.55rem;
  text-align: center;
}

.App {
  height: fit-content;
  padding: 0 7% 10% 7%;
  font-family: "Work Sans", sans-serif;
  font-weight: 500;
  color: #f1dcf7fa;
}
@media only screen and (min-width: 800px) {
  .App {
    padding: 0 14% 10%;
  }
}

.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}
